import React from "react";
import ReactDOM from "react-dom";

// import { Nav } from "@alifd/next";
// import { Nav } from "@alifd/meet-react";
//import { Form, Input, Radio, Field, Button } from '@alifd/next';
import { Form, Input, SafeArea, Field, Avatar } from "@alifd/meet-react";

const FormItem = Form.Item;

const formItemLayout = {
  labelCol: {
    span: 6
  },
  wrapperCol: {
    span: 14
  }
};

class App extends React.Component {
  field = new Field(this);

  checkUsername(rule, value, callback) {
    const { getValue } = this.field;
    if (value && value !== getValue("username")) {
      return callback("username");
    } else {
      return callback();
    }
  }
  render() {
    return (
      <div>
        <SafeArea></SafeArea>
        <div
          style={{
            backgroundColor: "#F5F5F5",
            padding: "0.5rem",
            flexDirection: "row"
          }}
        >
          <Avatar
            style={{
              backgroundColor: "#F5F5F5",
              display: "inline-block",
              width: "4rem",
              height: "4rem"
            }}
            shape="circle"
            size="large"
            src="./logo2.png"
          />
          <p
            style={{
              display: "inline-block",
              marginLeft: "12px",
              fontSize: "20px"
            }}
          >
            For Private Suite Draw
          </p>
        </div>
        <Form {...formItemLayout} field={this.field}>
          <FormItem
            label="Name:"
            hasFeedback
            required
            requiredMessage="Input your name"
            validator={this.checkUsername.bind(this)}
          >
            <Input name="username" />
          </FormItem>
          <FormItem
            label="Mobile Number:"
            hasFeedback
            required
            requiredMessage="Input Mobile Number"
            format="number"
            formatMessage="Invalid Mobile Number"
            minLength={8}
            maxLength={13}
            minmaxLengthMessage="Mobile Number length 8-13"
          >
            <Input name="mobile" />
          </FormItem>

          <FormItem wrapperCol={{ offset: 6 }}>
            <Form.Submit
              validate
              type="primary"
              onClick={async (v, e) => {
                console.log(v, e);
              }}
              style={{ margin: "0 10px" }}
            >
              Submit
            </Form.Submit>
          </FormItem>
        </Form>
      </div>
    );
  }
}

const rootElement = document.getElementById("root");

ReactDOM.render(<App />, rootElement);
